import React from 'react';
import Page from 'components/LayoutComponents/Page';
import Helmet from 'react-helmet';
import { Row, Col } from 'antd';
import 'video-react/dist/video-react.css';
import { Player } from 'video-react';
import InformationCard from './InformationCard';
import './style.scss';

class FacialRecognition extends React.Component {
  state = { visible: false };

  toggleModal = () => {
    this.setState({
      visible: !this.state.visible,
    });
  };

  render() {
    return (
      <Row>
        <Col span={8} id="left-content">
          <Row>
            <InformationCard />
            <InformationCard />
            <InformationCard />
            <InformationCard />
            <InformationCard />
            <InformationCard />
            <InformationCard />
          </Row>
        </Col>
        <Col span={8} id="video-area">
          <Player
            playsInline
            poster="/assets/poster.png"
            src="https://media.w3.org/2010/05/sintel/trailer_hd.mp4"
          />
        </Col>
        <Col span={8}>
          <img src="resources/images/personal.png" alt="avatar" id="personal-info" />
        </Col>
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <Helmet title="Facial Recognition" />
    <FacialRecognition {...props} />
  </Page>
);



