import React, { Component } from 'react';
import { Row, Col } from 'antd';
import './style.scss';

class InformationCard extends Component {
  state = {};
  render() {
    return (
      <Col span={24} className="information-card-outer">
        <div className="information-card">
          <Row>
            <Col id="avatar" span={8}>
              <img src="resources/images/avatars/1.jpg" alt="avatar" />
            </Col>
            <Col id="main-info" span={16}>
              <Row>
                <div id="title">Donald Johnson</div>
                <div id="time-past">3h ago</div>
              </Row>
              <Row>
                <div id="position">at HQ office</div>
                <div id="last-seen">Sep, 15th</div>
              </Row>
            </Col>
          </Row>
        </div>
      </Col>
    );
  }
}

export default InformationCard;



